export default {
	name:"InfoMaskTwo",
	props:{
		list:null
	},
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top
		}
	},
	methods: {
		operation(index) {
			if(index === this.list.length-1) {
				this.$emit("close");
				return;
			}else {
				this.$emit("close",index)
			}
		}
	},
	components:{
		
	}
}
